import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import {HttpModule} from '@angular/http';

import {MyApp} from './app.component';
import {HomePage} from '../pages/home/home';
import {MapPage} from '../pages/map/map';
import {LoginPage} from "../pages/login/login";
import {SubmitDataPage} from "../pages/submit-data/submit-data";
import {SubmitDataChooseCoordsPage} from "../pages/submit-data-choose-coords/submit-data-choose-coords";
import {ExplorePage} from "../pages/explore/explore";

import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {AngularFireModule} from "angularfire2";
import {AngularFireAuthModule} from "angularfire2/auth";
import {FIREBASE_CONFIG} from "../app.firebase.config";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {SubmitDataLandingPage} from "../pages/submit-data-landing/submit-data-landing";


@NgModule({
    declarations: [
        MyApp,
        MapPage,
        LoginPage,
        ExplorePage,
        SubmitDataLandingPage,
        SubmitDataPage,
        SubmitDataChooseCoordsPage,
    ],
    imports: [
        BrowserModule,
        IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/explore/explore.module#ExplorePageModule', name: 'ExplorePage', segment: 'explore', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/admin/admin.module#AdminPageModule', name: 'AdminPage', segment: 'admin', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-submit-data/edit-submit-data.module#AdminPageModule', name: 'EditSubmitDataPage', segment: 'edit-submit-data', priority: 'low', defaultHistory: [] }
  ]
}),
        AngularFireModule.initializeApp(FIREBASE_CONFIG),
        AngularFireAuthModule,
        FormsModule,
        ReactiveFormsModule,
        HttpModule
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        MapPage,
        LoginPage,
        ExplorePage,
        SubmitDataLandingPage,
        SubmitDataPage,
        SubmitDataChooseCoordsPage,
    ],
    providers: [
        StatusBar,
        SplashScreen,
        {provide: ErrorHandler, useClass: IonicErrorHandler}
    ]
})
export class AppModule {
}



